.btn{
    text-transform: capitalize;
}
.btn-brand{
    border-radius: 4px !important;
    box-shadow: none !important;
    padding: 13px 22px;
    line-height: 22px;
    font-family: 'Lato-Black';
    font-size: 18px;
    font-weight: 900;
    font-style: normal;
    font-stretch: normal;
    letter-spacing: normal;
} 

.btn-brand--danger{
    background: #FF5A5F;
    color: #FFF;
}

.btn-brand--yellow{
    background: $brand_color_yellow;
    color: #555555 !important;
}

.btn-brand--primary{
    background: #6EC0D4;
    color: #FFF;
}

.btn-brand--crimson{
    background: $brand-color-crimson;
    color: #FFFFFF;
}

.btn-brand--facebook{
    background: #334E8A;
    color: #FFFFFF;
}

.btn-brand--twitter{
    background: #4099FF;
    color: #FFFFFF;
}

.btn-brand--dark{
    background: $brand-color-dark;
    color: #FFFFFF;
}

.btn--brand-outline{
    background-color: transparent;
    &:hover{
        box-shadow: none !important;
    }
}

.btn--brand-outline-crimson{
    border: 1px solid $brand-color-crimson;
    color: $brand-color-crimson !important;
}

.btn--brand-outline-yellow{
    box-shadow: none;
    border: 1px solid $brand_color_yellow;
    color: $brand_color_yellow !important;
}

.btn--brand-round{
    border-radius: 40px !important;
}